.formulario{
    display: flex;
    justify-content: space-around;
    margin: 80px 50px;
    flex-wrap: wrap;
    gap: 20px;
}


.formulario form{
    flex: 1;
    background-color: #F7F7F7;
    box-shadow: 7px 7px 15px 0px rgba(0, 0, 0, 25%);
    border-radius: 20px;
    padding: 8px 100px;
}


.formulario h2{
    font-size: 32px;
    font-weight: 400px;
    color: rgba(33, 33, 33, 1);
}